<template>

<div>
  <!--
  <parallax :containerClass="classes.img" :sectionClass="classes.sec" :parallaxClass="classes.parallax" :speedFactor="0.7">
      <img src="../../assets/img/contact.jpg" alt="very cool bg">
  </parallax>
  -->
  <div class="contact-wrapper">

    <div class="contact-header">
    
        <div class="divide divide-50 offset-1 intro-id">
                <h3>Contact information</h3>
                <h5>Wanna get in touch, get a drink or simply say <span class="cl-prime"> hello </span> </h5>
        </div>

        <div class="divide divide-12 offset-1 contact-cv">
          <div class="text-center">
            <a class="resumeButton slide-i-o" target="_blank" href="../../../static/DH_resumev2.pdf"> downloadCV <i class="fa fa-file-pdf-o cl-prime"></i> </a>
          </div>
        </div>
        
      </div>
      <div class="line-decoration"></div>

      <div class="row-100">
      
        <div class="divide divide-50">

          <br>
          <h3> <span class="underline-r">social</span> </h3>

          <div class="social-wrapper">
            <div class="column column-3 text-center">

              <a target="_blank" href="https://www.facebook.com/danijel.hrcek.5" class="slide-i-o"> <i class="fa fa-facebook"></i> </a>

            </div>

            <div class="column column-3 text-center">

              <a target="_blank" href="http://instagram.com/danijel.hrcek" class="slide-i-o"> <i class="fa fa-instagram"></i> </a>

            </div>

            <div class="column column-3 text-center">

              <a target="_blank" href="http://hr.linkedin.com/in/danijelhrcek" class="slide-i-o"> <i class="fa fa-linkedin"></i> </a>

            </div>

            <div class="column column-3 text-center">

              <a target="_blank" href="https://github.com/DanijelH" class="slide-i-o"> <i class="fa fa-github"></i> </a>

            </div>
          </div>

          <div class="clearfix"></div>
          
          <h3> <span class="underline-r">email</span> </h3>
          
          <div class="column column-12">
            <p>
              <ul>
                <li> <i class="fa fa-paper-plane"></i> danijelhdev@gmail.com</li>
              </ul>
            </p>
          </div>
          
        </div> <!-- end divide -->

        <div class="divide divide-50 contact-location">
        
          <i class="fa fa-map-marker cl-prime"></i><br>
          <p>Zagreb <span class="cl-prime"> || </span>  Krapina - Croatia</p>

        </div>

      </div>

    </div>

    <FooterC></FooterC>
    
  </div>

</template>

<script>
  // import Parallax from 'vue-parallaxy'
  import Footer from '../footer/Footer.vue'

  let classData =
  {
    sec: 'paraxs',
    img: 'paraxi',
    parallax: 'parax'
  }

  export default {
    name: 'contact',
    components: {
      // Parallax,
      FooterC: Footer
    },
    data () {
      return {
        classes: {}
      }
    },
    created () {
      this.classes = classData
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

  @import "../../assets/settings.scss";

  .contact-header{
    overflow: auto;
    background-color: $primary-dark;
    padding: 5rem 1rem 2rem 1rem;
    color: white;
  }

  .contact-header h3 {
    text-align: left;
    margin: 1em 0em;
  }

  .contact-location{
    text-align: center;
  }

  .contact-location i{
    font-size: 15rem;
  }

  .contact-wrapper {
    min-height: calc(100vh - 100px);
    position: relative;
  }

  .contact-cv{
    margin-top: 2rem;
  }

  @media screen and (max-width: 780px) {
      .social-wrapper div{
        width: 24.916667%;
      }
  }

  .social-wrapper a {
    display: inline-block;
    font-size: 2em;
    color: $primary-dark;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    margin: 10px 0px;
    -webkit-transition: all 850ms;
    -moz-transition: all 850ms;
    -o-transition: all 850ms;
    -ms-transition: all 850ms;
    transition: all 850ms;
  }

  a.resumeButton{
    display: inline-block;
    margin: 25px 0;
    text-align: center;
    font-size: 1.5rem;
    color: white;
    text-decoration: none;
    border-bottom: 1px solid transparent;
    padding-bottom: 4px;
    -webkit-transition: all 850ms;
    -moz-transition: all 850ms;
    -o-transition: all 850ms;
    -ms-transition: all 850ms;
    transition: all 850ms;
  }

</style>



