      <div class="clearfix"></div>

      <div class="line-decoration-inverse"></div>
      <Skills></Skills>
      <div class="line-decoration"></div>

      <div class="home-contact-me text-center">
        <p class="">Interested? Get in touch.</p>
        <h1 class="cl-prime"> 
        <router-link to="/contact" class="slide-i-o cl-prime">
                           Contact information 
        </router-link>
        
        </h1>
