<script>

  import About from '../about/AboutInfo.vue'
  import Skills from '../skills/Skills.vue'
  import Contact from '../contact/Contact.vue'
  import Footer from '../footer/Footer.vue'

  let classData =
  {
    section: 'parallaxy-section',
    image: 'parallaxy-image',
    parallax: 'parallaxy'
  }

  export default {
    name: 'home',
    data () {
      return {
        fullName: 'Danijel Hrček',
        classes: {}
      }
    },
    components: {
      About,
      Skills,
      Contact,
      FooterC: Footer
    },
    created () {
      this.classes = classData
    }
  }
</script>

